<template>
  <div class="projectItem fn-clear">
    <span class="dataTime fontSE14 fn-left mt5">{{projectItem.dataTime}}</span>
    <svg class="icon fn-left" style="font-size: 26px; position: absolute; left: 260px;" aria-hidden="true">
      <use :xlink:href="'#icon-'+projectItem.icon"></use>
    </svg>
    <div class="companyProfile fn-left ml30">
        <span class="fontSE18 mb10 ml30">{{projectItem.name}}<span class="fontSE15 ml10 grey">{{projectItem.zz}}</span></span>
        <div class="list">
          <div v-for='(items,indexs) of projectItem.list' :key='indexs'>{{indexs+1}}、{{items}}</div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  props:['projectItem'],
}
</script>



