// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

/* eslint-disable no-new */
var apps = new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
})

$(document).ready(function(){

 var doughunt = {
		"oTx" : null,
		"load":function (obj,num){

      obj = typeof obj == 'object' ? obj : $(obj);
	        doughunt.oTx = obj.find(".tx");

			var old_num = doughunt.oTx.text();

			doughunt.transform(obj,num, old_num.substring(0,old_num.length-1));
            doughunt.setnum(num);
		},
		"transform":function (obj,num, old_num){
			var b_l_a = obj.find(".bar-left-an");
	        var b_r_a = obj.find(".bar-right-an");
	        var c_num = num;
	        if(c_num > 50) {
	            b_r_a.css({ "transform": "rotate(45deg)", "transition": "transform 1s linear" });
	            setTimeout(function() {
	                b_l_a.css({ "transform": "rotate(" + (((c_num-50)/100*360)-135) + "deg)", "transition": "transform 1s linear"  });
	            },1000);
	        } else {
	            if(old_num > 50) {
	                setTimeout(function() {
	                    b_r_a.css({  "transform": "rotate(" + ((c_num/100*360)-135) + "deg)", "transition": "transform 1s linear" });
	                },1000);
	                b_l_a.css({ "transform": "rotate(-135deg)", "transition": "transform 1s linear"});
	            } else {
	                b_r_a.css({  "transform": "rotate(" + ((c_num/100*360)-135) + "deg)",  "transition": "transform 1s linear" });
	            }

	        }
		},
		"setnum" : function(num) {
	        doughunt.oTx.text(num);
	    }
  }

  var decimal_places = 1,decimal_factor = 10,textIndex = true;
  var scrollTops = function(h){
    if(h >= 700){
      $(".nav_bottom").show();
    }else{
      $(".nav_bottom").hide();
    }

    if(h >= $(".headingBackground").height()){
      if($(".nav_top").height() == 0){
        $(".nav_top").show();
        $(".nav_top").stop().css({"height":"0"}).animate({"height":"160px"},"",function(){
          $(".nav_top").show();
        });
      }
    }else{
      if($(".nav_top").height() == 160){
        $(".nav_top").stop().css({"height":"160px"}).animate({"height":"0"},"",function(){
          $(".nav_top").hide();
        })
      }
    }

    if(h > $(".headingBackground").height()+500 && textIndex){
      $.each($(".cicle"), function (index, obj) {
        doughunt.load($(obj),$(obj).attr("param"));
        $(obj).find(".degree").animateNumber(
          {
            number: parseInt($(obj).find(".degree").html())*10,
            numberStep: function(now, tween) {
              var floored_number = Math.floor(now) / decimal_factor, target = $(tween.elem);
              if (decimal_places > 0) { floored_number = floored_number.toFixed(decimal_places); }
              floored_number = Math.floor(floored_number);
              target.text(floored_number + ' %');
            }
          },
          2500
        )
      });
      textIndex = false;
    }
  }

  var docHeight = document.documentElement.scrollTop || document.body.scrollTop;
  scrollTops(docHeight);

  window.onscroll = function() {
    var docHeights = document.documentElement.scrollTop || document.body.scrollTop;
    scrollTops(docHeights);
  }



  $(".nav_bottom ul li").click(function(){
    if($(this).find("div").css("display") == "none"){
      $(this).find("div").show();
    }else{
      $(this).find("div").hide();
    }

  })

  $(".nav_top ul li").click(function(){

    $("html,body").animate({scrollTop: $("."+$(this).attr("param")).offset().top}, 500);

    // document.querySelector("."+$(this).attr("param")).scrollIntoView();
  })

})




